/**
 * Conditional Form Fields Script (Generic & Reusable)
 *
 * This script enables conditional visibility and required-state for form fields
 * based on trigger inputs (like dropdowns or radios).
 *
 * ✅ How to use:
 * 1. Add `data-trigger="yourTriggerName"` to the input controlling the logic (e.g. <select> or <input>).
 * 2. Add `data-condition-trigger="yourTriggerName"` to any form field container that should be shown conditionally.
 * 3. Add `data-condition-value="triggerValue"` to define when that field should be visible.
 * 4. Inside the conditional container, set form elements to `required` — the script toggles this automatically.
 *
 * This allows multiple trigger/condition pairs on the same page without extra JS.
 */
export function initConditionalFormFields() {
  const triggers = document.querySelectorAll('[data-trigger]');

  triggers.forEach((trigger) => {
    trigger.addEventListener('change', () => updateConditionalFields(trigger));
    updateConditionalFields(trigger); // Run on load
  });

  function updateConditionalFields(trigger) {
    const triggerName = trigger.getAttribute('data-trigger');
    const triggerValue = trigger.value;

    const conditionals = document.querySelectorAll(`[data-condition-trigger="${triggerName}"]`);

    conditionals.forEach((field) => {
      const shouldShow = field.getAttribute('data-condition-value') === triggerValue;
      const input = field.matches('input, select, textarea') ? field : field.querySelector('input, select, textarea');

      field.style.display = shouldShow ? 'block' : 'none';
      if (input) {
        input.required = shouldShow;
        if (!shouldShow) input.value = '';
      }
    });
  }
}

/** This script
 * - loops through all custom dropdowns with "data-custom-dropdown" attribute
 * - sets the selected value as selected in the dropdown
 * - creates a hidden input for form data
 * - sets the selected value from "data-value" to the hidden input
 **/
export function initCustomDropdowns() {
  document.querySelectorAll('[data-custom-dropdown]').forEach((dropdown) => {
    // Create the hidden input
    const input = document.createElement('input');
    input.type = 'hidden';
    input.name = dropdown.dataset.name || 'custom-dropdown';
    input.setAttribute('required', dropdown.dataset.required === 'true' || false);
    input.value = ''; // Or set a default value if needed

    // Append it to the dropdown or its closest form
    dropdown.appendChild(input);

    const toggle = dropdown.querySelector('.dropdown-selected-value');
    const links = dropdown.querySelectorAll('.dropdown-item');

    links.forEach((link) => {
      link.addEventListener('click', (e) => {
        e.preventDefault();
        const value = link.dataset.value;
        const content = link.cloneNode(true);
        content.style.padding = 0; //reset paddings from dropdown-item

        // Update visual
        toggle.innerHTML = '';
        toggle.appendChild(content);

        // Set hidden field
        input.value = value;
      });
    });
  });
}



// Setting up daterange-pickers
// Docs: https://mymth.github.io/vanillajs-datepicker/
export function initDateRangePickers(groupSelector, startDateSelector, minimumDays = 30) {
  const datesInput = document.querySelector(groupSelector);
  const startDateField = document.querySelector(startDateSelector);
  const rangepicker = new DateRangePicker(datesInput, {
    format: 'dd. MM yyyy',
  });

  // Add an event listener to validate the date range
  datesInput.addEventListener('changeDate', validateDateRange(rangepicker, startDateField, minimumDays));
  return { rangepicker, datesInput, startDateField };
}

export function validateDateRange(rangepicker, startDateField, minimumDays = 30) {
  const [startDate, endDate] = rangepicker.getDates();

  if (startDate && endDate) {
    const differenceInDays = Math.floor((endDate - startDate) / (1000 * 60 * 60 * 24));
    if (differenceInDays < minimumDays) {
      startDateField.setCustomValidity(`You need to choose a minimum of ${minimumDays} days.`);
      // Set end date to {{minimumDays}} days after start date
      rangepicker.setDates(startDate, new Date(startDate.getTime() + minimumDays * 24 * 60 * 60 * 1000)); 
    } else {
      startDateField.setCustomValidity('');
    }
  } else {
    startDateField.setCustomValidity('');
  }

  // Trigger form validation
  startDateField.reportValidity();
}


/**
 * This script is responsible for
 *  - the navigation of the multi step field
 *  - update of the wizard step indicators
 *  - form validation of all form elements of the current step
 **/
export function initFormWizardSteps() {
  const steps = document.querySelectorAll('.form-step');
  const stepIndicators = document.querySelectorAll(
    '.form-wizard-step .form-wizard-step-progress'
  );
  let currentStep = 0;

  function showStep(index) {
    steps.forEach((step, i) => {
      step.style.display = i === index ? 'block' : 'none';
    });

    stepIndicators.forEach((indicator, i) => {
      if (i < index || i === index) {
        indicator.classList.add('active');
      } else {
        indicator.classList.remove('active');
      }
    });
  }

  function validateCurrentStep() {
    const currentInputs = steps[currentStep].querySelectorAll('input, select, textarea');
    const errors = [];

    currentInputs.forEach((input) => {
      const isHidden = input.type === 'hidden' || input.offsetParent === null;

      if (input.required) {
        const isEmpty = !input.value || input.value.trim() === '';
        if (isEmpty) {
          const label = input.dataset.name || input.name || input.placeholder || 'Unnamed field';
          errors.push(`${label} is required.`);
        }
      }

      // Custom type validation (e.g., email)
      if (!isHidden && input.value && !input.checkValidity()) {
        const label = input.name || input.placeholder || 'Unnamed field';
        errors.push(`${label} is invalid.`);
      }
    });
    return errors;
  }

  function showErrors(containerId, errorMessages) {
    const container = document.querySelector(containerId);

    if (!container) return;

    // Clear any previous errors
    container.innerHTML = '';

    // Create <ul> element
    const ul = document.createElement('ul');

    // Add each error as <li>
    errorMessages.forEach((msg) => {
      const li = document.createElement('li');
      li.textContent = msg;
      ul.appendChild(li);
    });

    // Append the list to the container
    container.appendChild(ul);
  }

  function resetErrors(containerId) {
    const container = document.querySelector(containerId);
    if (container) {
      container.innerHTML = '';
    }
  }

  document.querySelectorAll('.form-next-step-btn').forEach((btn) => {
    btn.addEventListener('click', () => {
      if (btn.getAttribute('disabled') === 'true') {
        return;
      }

      const errors = validateCurrentStep();
      const errorListId = `#error-list-step-${currentStep}`;
      resetErrors(errorListId);
      if (errors.length == 0) {
        currentStep = Math.min(currentStep + 1, steps.length - 1);
        const isLastStep = currentStep === steps.length - 1;
        if (isLastStep) {
          prepareReviewFields(); // this loops through all fields with #review-* and sets the corresponding value
        }
        showStep(currentStep);
      } else {
        showErrors(errorListId, errors);
      }
    });
  });

  function prepareReviewFields() {
    const reviewElements = document.querySelectorAll('[id^="review-"]');

    // Loop through the matched elements
    reviewElements.forEach((element) => {
      const fieldName = element.dataset.fieldname;
      try {
        const fieldValue = document.querySelector(`[name="${fieldName}"]`).value;
        element.innerText = fieldValue;
      } catch {}
    });
  }

  document.querySelectorAll('.form-previous-step-btn').forEach((btn) => {
    btn.addEventListener('click', () => {
      currentStep = Math.max(currentStep - 1, 0);
      showStep(currentStep);
    });
  });

  showStep(currentStep); // Init
}